export default "import { Table } from  \"af-virtual-scroll\";\n\nconst SimpleHeadlessTable = () => (\n    <Table\n        fixed\n        headless\n        rowsQuantity={1000}\n        getRowData={i => ({ a: `cell_a_${i}`, b: `cell_b_${i}`, c: `cell_c_${i}` })}\n        columns={[\n            { dataKey: \"a\", label: \"a\" },\n            { dataKey: \"b\", label: \"b\" },\n            { dataKey: \"c\", label: \"c\" }\n        ]}\n    />\n);\n\nexport default SimpleHeadlessTable;";